import { API_URL, getUser } from "../../constants/Config";
import * as events from "../events";

export const fetchTemplates = (page = 1, limit = 50) => {
  return async (dispatch, getState) => {
    const { team, token } = getUser();
    const { userCampaigns } = getState().campaigns;

    const ownerCampaign =
      userCampaigns &&
      userCampaigns.length > 0 &&
      userCampaigns.find((t) => t.createdUser._id === team);

    const body = {
      skip: page === 1 ? 0 : (page - 1) * limit,
      limit,
    };
    if (ownerCampaign) {
      const templates = [];
      ownerCampaign &&
        ownerCampaign.steps &&
        ownerCampaign.steps.map((s) => {
          if (s.emailtemplate) {
            templates.push(s.emailtemplate);
          }
          if (s.postcardtemplate) {
            templates.push(s.postcardtemplate);
          }
          return s;
        });

      if (templates.length > 0) {
        body["filter"] = { templates };
      }
    }

    try {
      const response = await fetch(`${API_URL}/templates/get`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: "Bearer " + token,
        },
        body: JSON.stringify(body),
      });

      if (!response.ok) {
        const errorResData = await response.json();
        console.log(errorResData);
        // throw new Error("Something went wrong!");
      }

      const resData = await response.json();

      dispatch({
        type: events.SET_TEMPLATES,
        data: resData.data ? resData.data : [],
        total: resData.count ? resData.count : 0
      });
    } catch (err) {
      // send to custom analytics server
      console.log("Error ", err);
      throw err;
    }
  };
};

export const deleteTemplate = (_id) => {
  return async (dispatch, getState) => {
    try {
      const { token } = getUser();
      let templates = getState().templates.userTemplates;
      const response = await fetch(`${API_URL}/templates/${_id}`, {
        method: "DELETE",
        headers: {
          "Content-Type": "application/json",
          Authorization: "Bearer " + token,
        },
        body: JSON.stringify({ active: false }),
      });
      if (!response.ok) {
        throw new Error("Something went wrong!");
      }
      const resData = await response.json();
      if (resData.data) {
        if (templates && templates.length > 0) {
          templates = templates.filter((f) => f._id !== resData.data._id);
        }
        await dispatch({
          type: events.SET_TEMPLATES,
          data: templates,
        });
        return true;
      }

      return false;
    } catch (err) {
      console.log(err);
      throw new Error(err.message);
    }
  };
};

export const saveTemplate = (data) => {
  return async (dispatch, getState) => {
    // any async code you want!
    const { token } = getUser();
    let templates = getState().templates.userTemplates;
    const { _id } = data;
    let url = `${API_URL}/templates/add`;
    let method = "POST";
    if (_id) {
      url = `${API_URL}/templates/${_id}`;
      delete data._id;
      method = "PATCH";
    }
    try {
      const response = await fetch(url, {
        method,
        headers: {
          "Content-Type": "application/json",
          Authorization: "Bearer " + token,
        },
        body: JSON.stringify(data),
      });

      if (!response.ok) {
        const errorRes = await response.json();
        let message = "Something went wrong!";
        if (errorRes && errorRes.error) {
          message = errorRes.error;
        }
        throw new Error(message);
      }

      const resData = await response.json();

      if (resData.data) {
        if (_id) {
          templates = templates.map((f) => {
            if (f._id === resData.data._id) {
              f = resData.data;
            }
            return f;
          });
        } else {
          templates = [...templates, resData.data];
        }

        dispatch({
          type: events.SET_TEMPLATES,
          data: templates,
        });
      }
    } catch (err) {
      console.log(err);
      throw new Error(err.message);
    }
  };
};
